.slick-dotted.slick-slider {
    margin-bottom: 0px;
    margin-top: 65px;
    @include MQ(S) {
        margin-top: 0px; } }
.slider {
    //height: 70vh
    position: relative;
    width: 100%;
    overflow: hidden;
    background-color: transparent;
    @include MQ(M) {
 }        //height: 90vh
    &--img {
        height: 77vh;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        @include image-rendering(pixelated);
        .info {
            position: absolute;
            bottom: 27%;
            left: 50%;
            right: auto;
            @include transform(translateX(-50%));
            vertical-align: middle;
            text-align: center;
            font-family: $three-font;
            b {
                font-weight: 700; } } } }

.slick {
    &-prev,
    &-next {
        top: inherit;
        bottom: 0%;
        width: 55px;
        height: auto;
        z-index: 99;
        border-radius: 0%;
        background-color:  $white;
        padding: 15px 0px;
        text-align: center;
        box-shadow: 0px 2px 2px #000;
        @include transition($trs-site);
        @include MQMAX(S) {
            top: inherit;
            bottom: 10px; } }
    &-prev {
        right: 65px;
        left: inherit;
        @include press {
            background-color: rgba($white, .9); }
        &:before:hover,
        &:before:focus {
            color: $fire !important; }
        @include MQMAX(S) {
            left: 105px; }
        &.slick-disabled {
            opacity: 1; } }
    &-next {
        right: 5px;
        @include press {
            background-color: rgba($white, .9); }
        @include MQMAX(S) {
            right: 105px; }
        &.slick-disabled {
            opacity: 1; } }
    &-prev:before,
    &-next:before {
        line-height: 1;
        color: $fire;
        opacity: .75;
        font-family: 'icomoon' !important;
        speak: none;
        font-weight: normal;
        font-variant: normal;
        text-transform: inherit;
        font-size: 25px; }

    &-prev {
        &:before {
            content: "\e93f";
            margin-right: 5px; } }
    &-next {
        &:before {
            content: "\e935";
            margin-left: 5px; } } }
