.cl {
    &--white {
        color: $white;
        @include heading {
            color: $white; } }
    &--silver {
        color: $silver;
        @include heading {
            color: $silver; } }
    &--steelblue {
        color: $steelblue;
        @include heading {
            color: $steelblue; } }
    &--darkturquoise {
        color: $darkturquoise;
        @include heading {
            color: $darkturquoise; } }
    &--green {
        color: $green;
        @include heading {
            color: $green; } }
    &--red {
        color: $red;
        @include heading {
            color: $red; } }
    &--fire {
        color: $fire;
        @include heading {
            color: $fire; } } }
