// header
.header {
    &-main {
        position: relative;
        left: 0;
        top: 0;
        z-index: 999;
        width: 100%;
        @include transition($trs-site);
        .info_box {
            position: relative;
            //float: left
            min-height: 35px;
            margin-left: 35px;
            font-size: 13px;
            text-transform: uppercase;
            padding: 0px 0px 0px 52px;
            display: inline-block;
            .icon_box {
                position: absolute;
                left: 0px;
                top: 2px;
                font-size: 40px;
                text-align: left;
                line-height: 1em;
                border-radius: 50%;
                @include transition($trs-site); }
            li {
                position: relative;
                line-height: 20px;
                font-size: 16px;
                margin-bottom: 3px;
                font-weight: 400;
                text-transform: capitalize;
                b {
                    font-weight: 400;
                    font-size: 20px;
                    color: $black; } } } }
    &-upper {
        position: fixed;
        z-index: 5;
        //background-color: transparent
        @include transition($trs-site);
        //display: none
        @include MQ(S) {
            position: relative; }
        @include MQ(M) {
            display: block; }
        .upper_right {
            width: inherit;
            padding-top: 5px;
            padding-bottom: 5px;
            text-align: left;
            // +MQ(S)
            // +MQ(M)
            @include MQ(M) {
                width: inherit;
                padding-top: 0px;
                padding-bottom: 5px;
                text-align: left; } } }
    &-lower {
        position: relative;
        top: 130px;
        width: 100%;
        z-index: 4;
        @include MQ(S) {
            top: 0px; }
        .bg-box {
            @include MQMAX(L) {
                display: none; } } } }
.nav {
    &-outer {
        position: relative;
        padding-left: 10px;
        padding-right: 210px;
        @include MQMAX(M) {
            margin: 0px 0px 15px;
            padding-left: 15px;
            padding-right: 15px; } } }

.main-menu {
    position: relative;
    @include MQMAX(S) {
        padding-top: 0px;
        width: 100%;
        margin: 0px;
        padding-left: 0px; }
    .navbar-header {
        @media only screen and (max-width: 767px) {
            position: relative;
            float: none;
            display: inline-block;
            text-align: right;
            width: 100%;
            padding: 13px 0px 14px;
            right: 0px;
            z-index: 12; } }
    .navigation {
        position: relative;
        margin: 0px;
        > li {
            position: relative;
            float: left;
            border-right: 1px solid rgba($white, .5);
            > a {
                position: relative;
                display: block;
                font-size: 18px;
                color: $black;
                line-height: 30px;
                opacity: 1;
                text-transform: capitalize;
                padding: 10px 20px;
                text-decoration: none;
                font-weight: 100;
                @include transition($trs-site);
                @include press {
                    color: $black;
                    opacity: 1; } }
            &.dropdown > a {
                padding-right: 40px;
                &:before {
                    font-family: 'icomoon' !important;
                    content: "\e93d";
                    position: absolute;
                    right: 10px;
                    font-size: 10px;
                    line-height: 32px; } }
            &.current > a {
                color: $black;
                opacity: 1; } } }
    .collapsing,
    .collapse {
        padding: 0px;
        @include MQMAX(S) {
            max-height: 300px;
            overflow: auto;
            float: none;
            width: 100%;
            padding: 10px 0px 0px;
            border: none;
            margin: 0px;
            border-radius: 3px; }
        &.in {
            @include MQMAX(S) {
                padding: 0px 0px 0px;
                border: none;
                margin: 0px 0px 15px;
                border-radius: 3px; } }
        & > .navigation {
            @include MQMAX(S) {
                float: none !important;
                margin: 0px !important;
                width: 100% !important;
 }                // background-color: $darkturquoise
            & > li {
                @include MQMAX(S) {
                    opacity: 1;
                    top: 0px;
                    left: 0px;
                    visibility: visible;
                    margin: 0px;
                    float: none;
                    width: 100%;
                    border-right: none; } } } }
    .navbar-toggle {
        @include MQMAX(S) {
            display: inline-block;
            z-index: 7;
            top: 1px;
            boder: 1px solid $darkturquoise;
            float: none;
            margin: 0px;
            border-radius: 0px;
            background-color: $darkturquoise; }
        .icon-bar {
            background: $white; } } }


.cd-dropdown-wrapper {
    display: inline-block;
    position: relative;
    margin-right: 0px;
    @include font-smoothing;
    width: 100%;
    @include MQ(S) {
        margin-right: 40%;
        width: auto; } }



.cd-dropdown-trigger {
    display: block;
    position: relative;
    padding: 0px 288.3px 4px 20px;
    line-height: 46px;
    background-color: $red;
    color: $white;
    border-radius: 0px;
    font-weight: 700;
    text-transform: uppercase;

    @include press {
      background-color: $fire;
      color: $white;
      text-decoration: none; }

    .no-touch {
        @include press {
          background-color: $fire;
          color: $white;
          text-decoration: none; } }

    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        margin-top: -1px;
        height: 2px;
        width: 9px;
        background: $white;
        @include backface-visibility(hidden);
        @include transition(width .3s, transform .3s); }

    &::before {
        right: 22px;
        @include transform(rotate(45deg)); }

    &::after {
        right: 17px;
        @include transform(rotate(-45deg)); }

    @include MQ(L) {
        font-size: 1.5rem;

        &.dropdown-is-active {
            background-color: $fire;
            color: $white;
            text-decoration: none;


            .no-touch &:hover {
                background-color: $fire; }


            &::before,
            &::after {
                width: 14px; }

            &::before {
                @include transform(translateX(5px) rotate(-45deg)); }

            &::after {
                @include transform(rotate(45deg)); } } } }


.cd-dropdown h2,
.cd-dropdown-content a,
.cd-dropdown-content ul a {
    height: 50px;
    line-height: 50px;

    @include MQ(M) {
        height: 60px;
        line-height: 60px; }

    @include MQ(L) {
        height: 50px;
        line-height: 50px; } }

.cd-dropdown-content .cd-divider {
    padding: 0 3px; }


.cd-dropdown-content a,
.cd-dropdown-content ul a {
    padding: 0 41px; }

.cd-dropdown {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $white;
    color: $red;
    visibility: hidden;
    @include backface-visibility(hidden);
    @include transform(translateZ(0));
    @include transform(translateY(-100%));
    @include transition(transform .5s 0s, visibility 0s .5s);
    padding-top: 150px;
    @include MQ(L) {
        padding-top: 0px; }

    h2 {
        position: relative;
        z-index: 1;
        color: $white;
        background-color: $red;
        border-bottom: 1px solid transparent;
        padding-top: 8px;
        padding-bottom: 50px;
        text-align: center;
        width: 100%;
        display: inline-block; }

    .cd-close {
        position: absolute;
        z-index: 1;
        right: 0;
        top: 160px;
        height: 50px;
        width: 50px;
        overflow: hidden;
        text-indent: 100%;
        white-space: nowrap;
        color: transparent;

        &::after,
        &::before {
            content: '';
            position: absolute;
            @include center;
            height: 20px;
            width: 2px;
            background-color: $white; }

        &::after {
            @include transform(translateX(-50%) translateY(-50%) rotate(45deg)); }

        &::before {
            @include transform(translateX(-50%) translateY(-50%) rotate(-45deg)); } }


    &.dropdown-is-active {
        visibility: visible;
        @include transform(translateY(0));
        @include transition(transform .5s 0s, visibility 0s 0s); }

    @include MQ(M) {
        .cd-close {
            top: 157px; } }


    @include MQ(L) {
        position: absolute;
        top: calc(100% - 2px);
        height: auto;
        width: auto;
        @include transform(translateX(0));
        background-color: $white;
        color: $red;
        box-shadow: 0 4px 12px rgba(#000, .1);
        opacity: 0;
        @include transform(translateY(30px));
        @include transition(opacity 0.3s 0s, visibility 0s 0.3s, transform 0.3s 0s);

        .open-to-left & {
            right: 0;
            left: auto; }

        h2, .cd-close {
            display: none; }

        &.dropdown-is-active {
            opacity: 1;
            @include transform(translateY(0));
            @include transition(opacity 0.3s 0s, visibility 0.3s 0s, transform 0.3s 0s); } } }

.cd-dropdown-content, .cd-dropdown-content ul {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    @include transition(transform 0.3s);
    padding-top: 210px;

    a {
        display: block;
        color: $red;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        border-top-width: 1px;
        border-color: transparent;
        border-style: solid;
        text-align: left; }


    li:first-of-type > a {
        border-top-width: 0; }

    li:last-of-type > a {
        border-bottom-width: 1px; }

    .cd-divider {
        padding-top: 1px;
        padding-bottom: 1px;
        background-color: $fire;
        font-size: 0rem;
        font-weight: bold;
        letter-spacing: 1px;
        color: transparent;
        text-transform: uppercase; }

    .cd-divider + li > a {
        border-top-width: 0; }

    a, .cd-search, .cd-divider {
        @include transform(translateZ(0));
        @include backface-visibility(hidden);
        @include transition(opacity .3s, transform .3s); }

    &.is-hidden {
        @include transform(translateX(100%)); }

    &.is-hidden > li > a,
    &.is-hidden > li > .cd-search,
    &.is-hidden > .cd-divider,
    &.move-out > li > a,
    &.move-out > li > .cd-search,
    &.move-out > .cd-divider {
        opacity: 0; }


    &.move-out > li > a,
    &.move-out > li > .cd-search,
    &.move-out > .cd-divider {
        @include transform(translateX(-100%)); }

    .dropdown-is-active & {
        -webkit-overflow-scrolling: touch; }

    @include MQ(M) {
        padding-top: 160px;

        a {
            font-size: 1.8rem; } }

    @include MQ(L) {
        padding-top: 0;
        overflow: visible;

        a {
            color: $red;
            height: 50px;
            line-height: 45px;
            font-size: 1.6rem;
            border-color: transparent;
            font-weight: 400; }

        .cd-divider {
            background-color: $fire;
            color: transparent;
            border-top: 1px solid transparent; }

        .cd-divider + li > a {
            border-top-width: 1px; }

        &.is-hidden > li > a,
        &.is-hidden > li > .cd-search,
        &.is-hidden > .cd-divider,
        &.move-out > li > a,
        &.move-out > li > .cd-search,
        &.move-out > .cd-divider {
            opacity: 1; } } }


@include MQ(L) {
  .cd-secondary-dropdown,
  .cd-dropdown-gallery,
  .cd-dropdown-icons {
      @include clearfix;
      @include transform(translateX(0));
      left: 100%;
      height: auto;
      background-color: $white;
      box-shadow: 0 2px 14px rgba(#000, .1); } }

.cd-dropdown-content {
    .see-all a {
        color: $red; }

    .cd-dropdown-gallery .cd-dropdown-item, .cd-dropdown-icons .cd-dropdown-item {
        height: 80px;
        line-height: 80px;

        h3 {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap; } }


    .cd-dropdown-gallery .cd-dropdown-item {
        padding-left: 90px; }

    .cd-dropdown-gallery img {
        position: absolute;
        display: block;
        height: 40px;
        width: auto;
        left: 20px;
        top: 50%;
        margin-top: -20px; }

    .cd-dropdown-icons .cd-dropdown-item {
        padding-left: 75px;
        position: relative;

        p {
            color: $red;
            font-size: 1.3rem;
            display: none; }

        &::before {
            content: '';
            display: block;
            position: absolute;
            left: 20px;
            top: 50%;
            margin-top: -20px;
            width: 40px;
            height: 40px;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 40px 40px; }

        &.item-1::before {
            background-image: url('../img/nucleo-icon-1.svg'); }

        &.item-2::before {
            background-image: url('../img/nucleo-icon-2.svg'); }

        &.item-3::before {
            background-image: url('../img/nucleo-icon-3.svg'); }

        &.item-4::before {
            background-image: url('../img/nucleo-icon-4.svg'); }

        &.item-5::before {
            background-image: url('../img/nucleo-icon-5.svg'); }

        &.item-6::before {
            background-image: url('../img/nucleo-icon-6.svg'); }

        &.item-7::before {
            background-image: url('../img/nucleo-icon-7.svg'); }

        &.item-8::before {
            background-image: url('../img/nucleo-icon-8.svg'); }

        &.item-9::before {
            background-image: url('../img/nucleo-icon-9.svg'); }

        &.item-10::before {
            background-image: url('../img/nucleo-icon-10.svg'); }

        &.item-11::before {
            background-image: url('../img/nucleo-icon-11.svg'); }

        &.item-12::before {
            background-image: url('../img/nucleo-icon-12.svg'); } }


    @include MQ(L) {
        position: static;
        height: auto;
        width: 280px;

        > li:last-of-type a {
            border-bottom: none; }

        .no-touch & > li:not(.has-children) a:hover {
            color: $red; }

        &.move-out > li > a,
        &.move-out > li > .cd-search,
        &.move-out > .cd-divider {
            @include transform(translateX(0));
            opacity: 1; }

        .cd-secondary-dropdown,
        .cd-dropdown-gallery,
        .cd-dropdown-icons {
            @include clearfix;
            @include transform(translateX(0));
            left: 100%;
            height: auto;
            background-color: $white;
            box-shadow: 0 2px 14px rgba(#000, .1);

            .open-to-left & {
                left: auto;
                right: 100%; }

            &.is-hidden {
                @include transform(translateX(0)); }

            &.fade-in {
                @include animation(cd-fade-in .2s); }

            &.fade-out {
                @include animation(cd-fade-out .2s); }


            > .go-back {
                display: none; }

            > .see-all {
                position: absolute;
                bottom: 20px;
                height: 45px;
                text-align: center;

                a {
                    margin: 0;
                    height: 100%;
                    line-height: 45px;
                    background: $red;
                    color: $white;
                    pointer-events: auto;
                    @include transition(color .2s, background-color .2s);
                    text-align: center;

                    .no-touch &:hover {
                        color: $white;
                        background-color: $fire; } } }

            .cd-dropdown-item, a {
                border: none; } }

        .cd-dropdown-gallery, .cd-dropdown-icons {
            padding: 20px 30px 100px;

            > .see-all {
                width: calc(100% - 60px); } }

        .cd-dropdown-icons > li, .cd-secondary-dropdown > li {
            @include column(0.5); }

        .cd-secondary-dropdown {
            overflow: hidden;
            width: 550px;
            padding-bottom: 65px;

            &::before {
                position: absolute;
                content: '';
                top: 290px;
                left: 15px;
                height: 1px;
                width: 520px;
                background-color: $white; }

            > li > a {
                color: $red;
                font-size: 1.6rem;
                margin-bottom: 10px;
                line-height: 30px;
                height: 30px;
                pointer-events: none;

                &::after, &::before {
                    display: none; } }

            &.move-out > li > a {
                @include transform(translateX(0)); }


            > li {
                margin: 20px 0;
                border-right-width: 1px;
                border-color: transparent;
                border-style: solid;
                padding: 0 30px;
                max-height: 250px; }

            > li:nth-of-type(2n) {
                border-right-width: 0; }

            > li > ul {
                @include transform(translate(0));
                position: relative;
                height: auto;

                > .go-back {
                    display: none; } }


            a {
                line-height: 25px;
                height: 25px;
                font-size: 1.3rem;
                padding-left: 0;

                .no-touch &:hover {
                    color: $red; } }

            ul {
                padding-bottom: 25px;
                overflow: hidden;
                height: auto; }

            .go-back a {
                padding-left: 20px;
                color: $red;

                .no-touch &:hover {
                    color: $fire; }


                &::before,
                &::after {
                    left: 0; } }

            .see-all {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%; } }

        .cd-dropdown-gallery {
            width: 600px;
            padding-bottom: 100px;

            > li {
                @include column(0.48);
                margin-right: 4%;

                &:nth-of-type(2n) {
                    margin-right: 0; } }

            .cd-dropdown-item {
                padding: 0;
                height: auto;
                line-height: normal;
                color: $red;
                margin-bottom: 2em; }

            > li:nth-last-of-type(2) a,
            > li:last-of-type a {
                margin-bottom: 0; }

            img {
                position: static;
                height: auto;
                width: 100%;
                margin: 0 0 0.6em; } }

        .cd-dropdown-icons {
            width: 600px;

            .cd-dropdown-item {
                height: 80px;
                line-height: 1.2;
                padding: 24px 0 0 85px;

                &:hover {
                    background: $white; }

                h3 {
                    color: $red;
                    font-weight: bold; }

                p {
                    display: block;
                    font-size: 1.2rem; }

                &::before {
                    left: 25px; } } }

        > .has-children > ul {
            visibility: hidden; }

        > .has-children > ul.is-active {
            visibility: visible; }

        > .has-children > .cd-secondary-dropdown.is-active > li > ul {
            visibility: visible; }

        > .has-children > a.is-active {
            box-shadow: inset 2px 0 0 $red;
            color: $red;

            &::before,
            &::after {
                background: $gray; } }


        .open-to-left & > .has-children > a.is-active {
            box-shadow: inset -2px 0 0 $gainsboro; } } }


.cd-search input[type="search"] {
    width: 100%;
    height: 50px;
    padding: 0 20px;
    @include appearance(none);
    background-color: $white;
    color: $red;
    border: none;
    border-radius: 0;

    @include placeholder {
        color: $fire; }

    &:focus {
        background: $white;
        color: $red;
        outline: none;

        @include placeholder {
            color: $fire; } }

    @include MQ(L) {
        background-color: $gainsboro;

        @include placeholder {
            color: $fire; } } }

.has-children > a,
.go-back a {
    position: relative;

    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        margin-top: -1px;
        display: inline-block;
        height: 2px;
        width: 10px;
        background: $red;
        @include backface-visibility(hidden); }

    &::before {
        @include transform(rotate(45deg)); }


    &::after {
        @include transform(rotate(-45deg)); }

    @include MQ(L) {
        &::before,
        &::after {
            background: $fire; } } }


.has-children > a {
    padding-right: 40px;

    &::before,
    &::after {
        right: 20px;
        @include transform-origin(9px 50%); } }

@include MQ(L) {
    .open-to-left .cd-dropdown-content > .has-children > a {
        padding-left: 40px;
        padding-right: 20px;

        &::before,
        &::after {
            right: auto;
            left: 20px;
            @include transform-origin(1px 50%); } } }

.cd-dropdown-content .go-back a {
    padding-left: 40px;

    &::before,
    &::after {
        left: 20px;
        @include transform-origin(1px 50%); } }


.cd-main-content {
    background-color: $white;
    min-height: calc(100vh - 100px);
    padding: 2em 5%;
    line-height: 2; }


.no-js .cd-dropdown-wrapper:hover .cd-dropdown {
    visibility: visible;
    opacity: 1;
    @include transform(translateY(0)); }


.no-js .cd-dropdown-wrapper .cd-close {
    display: none; }

.mx_header {
    max-height: 76px; }
@include MQ(L) {
    .see-all_movie {
        display: none; } }
