// forms

fieldset {
    border: none; }

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
    border: none;
    -webkit-text-fill-color: $steelblue;
    -webkit-box-shadow: 0 0 0px 1000px $white inset;
    transition: background-color 5000s ease-in-out 0s; }

.newslatter {
    position: relative;
    .register.error {
        input.newslatter_input {
            border: 1px solid red; } }
    #{$all-text-inputs} {
        &.newslatter_input {
            outline: none;
            margin-top: 1em;
            padding:  0px 4em 0px 1.9em;
            height: 40px;
            width: 100%;
            background: rgba($white, 1);
            color: $gray;
            position: relative;
            display: block;
            border: none;
            font-size: 13px;
            border-radius: 20px;
            -webkit-appearance: none;
            @include transition(all 0.3s ease-in-out);
            &:focus {
                + label {
                    &:after {
                        @include transform(translate3d(0, 0, 0)); }
                    span {
                        animation: anim-1 0.3s forwards; } } } } } }
.contact_form {
    position: relative;
    from {
        width: 100%; }
    #{$all-text-inputs} {
        &.contac_input {
            outline: none;
            margin-top: .4em;
            margin-bottom: 1em;
            padding:  0px 4em 0px 1em;
            height: 40px;
            width: 100%;
            background: gainsboro;
            color: $red;
            position: relative;
            display: block;
            border: none;
            font-size: 13px;
            border-radius: none;
            border: 0px solid transparent;
            -webkit-appearance: none;
            @include transition(all 0.3s ease-in-out);
            @include placeholder {
                color: $fire; }
            &:focus {
                + label {
                    &:after {
                        @include transform(translate3d(0, 0, 0)); }
                    span {
                        animation: anim-1 0.3s forwards; } } } }
        &.contac_txtArea {
            outline: none;
            margin-top: .4em;
            margin-bottom: 1em;
            padding:  1em 4em 1em 1em;
            width: 100%;
            background: gainsboro;
            color: $red;
            position: relative;
            display: block;
            border: none;
            font-size: 13px;
            border-radius: none;
            border: 0px solid transparent;
            -webkit-appearance: none;
            @include transition(all 0.3s ease-in-out);
            @include placeholder {
                color: $fire; }
            &:focus {
                + label {
                    &:after {
                        @include transform(translate3d(0, 0, 0)); }
                    span {
                        animation: anim-1 0.3s forwards; } } } } } }
.contact_form {
    label {
        position: relative;
        width: 100%;
        display: block; } }
.affair {
    &.error {
        label {
            &:before {
                content: "Escriba el asunto."; } } } }
.fullName {
    &.error {
        label {
            &:before {
                content: "Escriba su Nomble completo."; } } } }
.email {
    &.error {
        label {
            &:before {
                content: "Escriba su Email."; } } } }
.message {
    &.error {
        label {
            &:before {
                content: "Escriba su mensaje para nosotros."; } } } }


.error {
    label {
        &:before {
            font-size: 10px;
            color: red;
            position: absolute;
            right: 0; } } }
.form_column {
    position: relative;
    display: inline-block;
    padding: 95px 15px 100px 100px;
    vertical-align: top;
    @include MQMAX(S) {
        padding: 95px 15px; } }
