// buttons
.btn {
    border: none;
    box-shadow: none;
    border-radius: inherit;
    font-size: 1.4rem;
    font-family: $primary-font;
    padding: 0.8em 1.2em;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 600;
    -webkit-appearance: inherit;
    @include transition(all 0.3s ease-in-out);
    &_ghost {
        @include btnGhost; }
    &-box {
        z-index: 12;
        margin-top: 0px;
        display: none;
        width: 70%;
        position: relative;
        @include MQ(L) {
            display: inline-block;
            width: auto; } }
    &-full {
        width: 100%; }
    &_quote {
        position: relative;
        line-height: 24px;
        color: $white;
        font-size: 15px;
        font-weight: 700;
        padding: 8px 30px;
        background-color: $fire;
        text-transform: uppercase;
        border: 2px solid transparent;
        top: 8px;
        @include press {
            background-color: $firedark;
            color: $white; }
        @include MQ(M) {
            top: inherit;
            padding: 11px 52px; } }
    &_site {
        background-color: $red;
        color: $white;
        @include press {
            background-color: $fire;
            color: $white; }
        span.icon {
            &:after {
                content: "+";
                margin-right: 12px; } }
        &--gray {
            background-color: silver;
            color: $white; }
        &--green {
            background-color: $green;
            color: $white; }
        &--gold {
            background-color: $gold;
            color: $white; }
        &--orange {
            background-color: $darkorange;
            color: $white; }
        &--red {
            background-color: $red;
            color: $white !important;
            @include press {
                background-color: $fire;
                color: $white; } } } }
.disabled {
    cursor: no-drop;
    @include press {
        color: $silver;
        p {
            color: $silver; } } }
