// footer
.menu-button {
  position: fixed;
  bottom: 0.5em;
  left: .5em;
  z-index: 1002;
  margin: 10px;
  width: 4.5em;
  height: 4.5em;
  border-radius: 50%;
  background-color: $silver;
  cursor: pointer;
  outline: none;
  vertical-align: middle;
  @include press {
    outline: none; }
  > span {
    font-size: 2.5em;
    line-height: 1.5rem;
    vertical-align: middle; } }

.menu-wrap {
  position: fixed;
  z-index: 1001;
  background: $white;
  -webkit-transition: width 0.3s, height 0.3s;
  transition: width 0.3s, height 0.3s;
  width: 0;
  height: 0;
  font-size: 1.5em;
  bottom: 1em;
  left: 1em;
  -webkit-transform: 0% 100%;
  transform-origin: 0% 100%; }
.menu {
  height: 100%;
  opacity: 0;
  font-size: 0.65em;
  color: $red;
  text-align: left; }

.show-menu {
  .wrap::before {
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    @include MQ(M) {
      opacity: 1; } }
  .menu-wrap {
    width: 280px;
    height: 455px;
    -webkit-animation: anim-jelly 0.8s linear forwards;
    animation: anim-jelly 0.8s linear forwards; }
  .menu {
    opacity: 1;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s; } }

.show-menu {
  .wrap {
    //+MQMAX(S)
    //width: 50vw
    //position: relative

    &::before {
      //position: absolute
      top: 0;
      left: 0;
      z-index: 1000;
      //width: 200%
      height: 100%;
      background: rgba(0,0,0,0.3);
      content: '';
      opacity: 0;
      -webkit-transition: opacity 0.3s, -webkit-transform 0s 0.3s;
      transition: opacity 0.3s, transform 0s 0.3s;
      -webkit-transform: translate3d(100%,0,0);
      transform: translate3d(100%,0,0);
      @include MQ(S) {
        width: 100%; } } } }
